/* Reset some default styling */
body,
h1, h2, h3, h4, h5, h6,
p, figure,
blockquote, dl, dd, ol, ul,
fieldset, legend {
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Arial', sans-serif;
  line-height: 1.6;
}

/* Header */
#header {
  background-color: #1c1c1c;
  color: #ffffff;
  padding: 20px 0;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
}

#header h1 a {
  color: #ffffff;
  font-size: 32px;
  text-decoration: none;
}

#header h2 {
  font-size: 18px;
  margin-top: 4px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar ul {
  list-style: none;
  display: flex;
  margin: 0;
}

.navbar li {
  margin: 0 20px;
}

.navbar a {
  color: #ffffff;
  text-decoration: none;
  font-size: 18px;
  transition: color 0.3s;
}

.navbar a:hover {
  color: #e44d26;
}

/* Sections */
.container {
  width: 80%;
  margin: 0 auto;
}

.section-title h2 {
  font-size: 36px;
  margin-bottom: 20px;
}

.section-title p {
  font-size: 18px;
  color: #777;
}

/* Home Section */
#home {
  background-color: #f5f5f5;
  color: #333;
  padding: 100px 0;
  text-align: center;
}

#home h1 {
  font-size: 48px;
  margin-bottom: 20px;
}

#home p {
  font-size: 18px;
  color: #777;
}

/* ... (other sections styles) */

/* Footer */
footer {
  background-color: #1c1c1c;
  color: #ffffff;
  text-align: center;
  padding: 20px 0;
  position: fixed;
  width: 100%;
  bottom: 0;
}
